import { Component, OnInit } from '@angular/core';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-search-services',
  templateUrl: './search-services.component.html',
  styleUrls: ['./search-services.component.scss']
})
export class SearchServicesComponent {
  public serviceList: any[] = this.vhQueryCafe.getlocalProducts().filter(product => product.type == 2);
  public searchList: any[] = [];
  constructor(
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm
  ) {
    this.searchList = [... this.serviceList]
  }

  cancel(value) {
    if (value) this.vhComponent.hideModal()
  }
  selectServices(value) {
    if (value) this.vhComponent.hideModal(value)
  }

  search(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.serviceList, ['name', 'barcode'])
    else this.searchList = this.serviceList
  }

}