import { Component, Input } from '@angular/core';
import { AnimationController } from '@ionic/angular';
import { VhQueryCafe, VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from '../../vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
@Component({
  selector: 'app-search-foods',
  templateUrl: './search-foods.component.html',
  styleUrls: ['./search-foods.component.scss']
})
export class SearchFoodsComponent {
  @Input() selectOption: boolean = true;
  public foodList: any[] = [];
  public searchList: any[] = [];
  public choiceFood: any = { subs: [] };
  public typeGoods: 1 | 2 = 1;
  constructor(
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private animationCtrl: AnimationController,
    private languageService: LanguageService
  ) {
    this.changeType(this.typeGoods)
  }

  type: 'show' | 'hide' = 'hide'

  viewInfo(type: 'show' | 'hide' = 'show') {
    this.type = type
    if (type == 'show')
      this.animationCtrl.create('view-info-popup-show').addElement(document.querySelector('.view-info-popup')).duration(300)
        .easing('cubic-bezier(0.16, 1, 0.3, 1)')
        .fromTo('transform', 'translateY(100%)', 'translateY(0)')
        .fromTo('visibility', 'hidden', 'visible').play()

    if (type == 'hide')
      this.animationCtrl.create('view-info-popup-hide').addElement(document.querySelector('.view-info-popup')).duration(300)
        .easing('cubic-bezier(0.16, 1, 0.3, 1)')
        .fromTo('transform', 'translateY(0)', 'translateY(100%)')
        .fromTo('visibility', 'visible', 'hidden').play()
  }

  cancel(value) {
    if (value) this.vhComponent.hideModal()
  }
  selectFoods(value) {
    if (this.selectOption) {
      if (value.manysize) {
        this.choiceFood = {
          ...value, subs: value.subs.map(item => { return { ...item, quantity: 0, note: '', price_origin: item.price } })
        }
      } else {
        this.choiceFood = { ...value, quantity: 0, note: '', price_origin: value.price }
      }

      if (this.choiceFood) this.viewInfo('show')
    } else this.vhComponent.hideModal(value)
  }

  changeType(value: number) {
    if (value == 1) this.foodList = this.vhQueryCafe.getlocalProducts(13).filter(good => !good.delete_hidden);
    else if (value == 2) this.foodList = this.vhQueryCafe.getlocalProducts(2).filter(good => !good.delete_hidden);
    this.search('')
  }

  search(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.foodList, ['name'])
    else this.searchList = this.foodList
  }

  /** Chnh sa sn phm */
  editFood(sub) {
    this.vhComponent.alertInputRadioCheckbox(
      this.languageService.translate("Edit"), sub.name, this.languageService.translate("Save"), this.languageService.translate('Cancel'),
      [
        {
          name: "quantity", type: 'number', placeholder: this.languageService.translate('Quantity'), value: sub.quantity, cssClass: "selling-quantity", id: "selling-quantity",
          attributes: {
            min: 1,
            max: 99999999
          }
        },
        {
          name: "note", type: 'text', placeholder: this.languageService.translate('Note...'), value: sub.note, cssClass: "selling-note", id: "selling-note",
        }
      ]
    ).then(result => {
      let number: string = document.getElementById('selling-quantity')['value']
      sub['quantity'] = parseFloat(number) < 1 || !number.length ? 1 : parseFloat(number)
      sub['note'] = document.getElementById('selling-note')['value']
    }, () => { })
  }

  subQuantity(item) {
    if (item.quantity > 0) item.quantity--
  }
  addQuantity(item) {
    item.quantity++
  }

  addFood() {
    if (this.choiceFood) {
      this.vhComponent.hideModal(this.choiceFood)
    }
  }
}
