import { Component, ChangeDetectorRef } from '@angular/core';
import { VhComponent } from '../../vh-component/vh-component';
import { VhAlgorithm } from 'ionic-vhframeworks';


@Component({
  selector: 'app-search-branch',
  templateUrl: './search-branch.component.html',
  styleUrls: ['./search-branch.component.scss']
})
export class SearchBranchComponent {

  public branchList: any[] = [
    {
      id: 1,
      name: 'Main store',
    },
    {
      id: 2,
      name: 'Viethas',
    },
    {
      id: 3,
      name: 'Vietcast',
    },
  ]
  public searchList: any[] = [];

  constructor(
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef


  ) { }

  ngAfterViewInit() {
    
    this.searchList = [... this.branchList]
    this.cdRef.detectChanges()
  }

  cancel(value) {
    if (value) this.vhComponent.hideModal()
  }
  selectBranch(value) {
    if (value) this.vhComponent.hideModal(value)
  }
  search(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.branchList, ['name'])
    else this.searchList = this.branchList
  }

}
