import { Component } from '@angular/core';
import { Platform } from '@ionic/angular';
import { VhQueryCafe, VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from '../../vh-component/vh-component';
@Component({
  selector: 'app-search-ingredient-grid',
  templateUrl: './search-ingredient-grid.component.html',
  styleUrls: ['./search-ingredient-grid.component.scss']
})
export class SearchIngredientGridComponent {
  public typeProducts: 1 | 2 | 3 = 3;
  public ingredientList: any[] = []
  public searchList: any[] = [];
  constructor(
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private platform: Platform
  ) {
    this.ingredientList = this.vhQueryCafe.getlocalProducts().filter(ingredient => ingredient.type == 3)
    this.search('')
  }

  public myTrackByFunction(index: number, complexItem: any): number {
    return complexItem._id;
  }
  itemHeightFn(item, index) { // better performance if setting item height
    return 258;
  }

  cancel(value) {
    if (value) this.vhComponent.hideModal()
  }
  selectProducts(value) {
    if (value) this.vhComponent.hideModal(value)
  }

  search(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.ingredientList, ['name'])
    else this.searchList = this.ingredientList

    this.searchList = this.transform()
  }

  transform() {
    if (this.platform.width() >= 599) return this.transformProd3()
    else return this.transformProd2()
  }
  transformProd2() {
    let array: any[] = []
    let arrayRaw = this.searchList.map((item, index) => {
      return this.searchList[index + 1] ? [item, this.searchList[index + 1]] : [item]
    })

    let val = this.searchList.length
    for (let i = 0, len = val <= 1 ? val : ((val % 2) ? val : (val - 1)); i < len; i += 2) {
      array.push(arrayRaw[i])
    }
    return array;
  }
  transformProd3() {
    let array: any[] = []
    let arrayRaw = this.searchList.map((item, index) => {
      if (!this.searchList[index + 1]) return [item]
      else if (!this.searchList[index + 2]) return [item, this.searchList[index + 1]]
      else return [item, this.searchList[index + 1], this.searchList[index + 2]]
    })
    let val = this.searchList.length
    for (let i = 0, len = val <= 1 ? val : ((val % 2) ? val : (val - 1)); i < len; i += 3) {
      array.push(arrayRaw[i])
    }
    return array;
  }
  transformProd4() {
    let array: any[] = []
    let arrayRaw = this.searchList.map((item, index) => {
      if (!this.searchList[index + 1]) return [item]
      else if (!this.searchList[index + 2]) return [item, this.searchList[index + 1]]
      else if (!this.searchList[index + 3]) return [item, this.searchList[index + 1], this.searchList[index + 2]]
      else return [item, this.searchList[index + 1], this.searchList[index + 2], this.searchList[index + 3]]
    })
    let val = this.searchList.length
    for (let i = 0, len = val <= 1 ? val : ((val % 2) ? val : (val - 1)); i < len; i += 4) {
      array.push(arrayRaw[i])
    }
    return array;
  }

}

