import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';


@Component({
  selector: 'app-search-customer',
  templateUrl: './search-customer.component.html',
  styleUrls: ['./search-customer.component.scss']
})
export class SearchCustomerComponent implements OnInit {
  @Input("id_customer") id_customer;
  @Output() closeAndReceiveCustomer = new EventEmitter<any>();
  debt: boolean = null;
  public customerList: any[] = []

  public searchList: any[] = [];
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private vhcomponent: VhComponent,
    private languageService: LanguageService,
  ) {

  }
  ngOnInit() {

  }
  cancel(value) {
    this.closeAndReceiveCustomer.emit(value)
  }

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    document.getElementById("select-customer").click()
  }


  inputValue = '';
  selectedValue: any = null;

  isFocused = false;

  onFocusInput() {
    this.isFocused = true;
    if (this.searchList.length) this.isOpen = true;

    // Nu  chn gi tr ri, th khi focus  xa inputValue  user nhp mi
    if (this.selectedValue) {
      this.inputValue = '';
    }
  }

  onBlurInput() {
    this.isFocused = false;
    setTimeout(() => {
      this.isOpen = false;
    }, 100);

    // Nu ngi dng khng nhp g mi th gi li kt qu  chn
    if (this.selectedValue)
      this.inputValue = this.selectedValue.display;
  }


  onKeyDown(event: KeyboardEvent) {

    if (event.key === 'Enter') {
      const searchValue = this.vhAlgorithm.changeAlias(this.inputValue.trim().toLowerCase());
      console.log(searchValue);

      event.preventDefault();
      event.stopPropagation();
      if (searchValue.length >= 6) {
        this.vhQueryCafe.searchKeyword_NamePhone('customers', searchValue).then((res: any) => {
          this.isOpen = true;
          console.log(res);

          this.searchList = res.map(item => {
            return { ...item, earning_point: item.earning_point ? item.earning_point : 0, display: item.name + ' - ' + item.phone }
          })
          if (this.searchList.length == 0) {
            this.vhcomponent.alertMessageDesktop("warning", this.languageService.translate('No data'))
          }
          if (this.searchList.length == 1) {
            this.onSelect(this.searchList[0])
          }
        })
      }
      else  this.vhcomponent.alertMessageDesktop("warning", this.languageService.translate('Please enter full infomation'))
    }
  }

  isOpen = false;
  onSelect(result: any) {
    this.selectedValue = result;
    this.inputValue = result.display;
    this.isOpen = false;
    this.id_customer = this.selectedValue._id;
    this.cancel(this.id_customer)

    // this.searchList = [];
  }

}
