import { Component, OnInit } from '@angular/core';
import { AnimationController } from '@ionic/angular';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from '../../vh-component/vh-component';

@Component({
  selector: 'app-search-dishes',
  templateUrl: './search-dishes.component.html',
  styleUrls: ['./search-dishes.component.scss']
})
export class SearchDishesComponent {
  public foodList: any[] = [];
  public searchList: any[] = [];
  constructor(
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
  ) {
    this.changeType()
  }

  type: 'show' | 'hide' = 'hide'

  cancel(value) {
    if (value) this.vhComponent.hideModal()
  }
  selectFoods(value) {
    this.vhComponent.hideModal(value)
  }

  changeType() {
    this.foodList = this.vhQueryCafe.getlocalProducts(1)
    this.search('')
  }

  search(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.foodList, ['name'])
    else this.searchList = this.foodList
  }
}
