import { ChangeDetectorRef, Component, Input } from '@angular/core';
import { VhQueryCafe, VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from '../../vh-component/vh-component';

@Component({
  selector: 'app-search-area',
  templateUrl: './search-area.component.html',
  styleUrls: ['./search-area.component.scss']
})
export class SearchAreaComponent {
  public areaList: any[] = []
  public searchList: any[] = [];
  constructor(
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef
  ) { }

  ngAfterViewInit() {
    this.areaList = this.vhQueryCafe.getlocalAreas().filter(item => item.id_branch == this.vhQueryCafe.getDefaultBranch()._id)
    this.searchList = [... this.areaList]
    this.cdRef.detectChanges()
  }

  cancel(value) {
    if (value) this.vhComponent.hideModal()
  }
  selectArea(value) {
    if (value) this.vhComponent.hideModal(value)
  }

  search(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.areaList, ['name'])
    else this.searchList = this.areaList
  }
}
