import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Platform } from '@ionic/angular';
import { VhQueryCafe, VhQuery, VhAlgorithm, VhAuth } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from '../../vh-component/vh-component';

@Component({
  selector: 'app-search-tables',
  templateUrl: './search-tables.component.html',
  styleUrls: ['./search-tables.component.scss']
})
export class SearchTablesComponent implements OnInit {
  @Input() status: boolean | null = null;
  public areaID: any = '';
  public area: any[] = []
  public table: any[] = [];
  public refreshStatus: boolean = false;
  constructor(
    private vhComponent: VhComponent,
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private vhQuery: VhQuery,
    private languageService: LanguageService,
    private platform: Platform,
    private activatedRoute: ActivatedRoute,
    private vhAuth: VhAuth
  ) { }

  ngOnInit() {
    this.getArea()
  }

  public myTrackByFunction(index: number, complexItem: any): number {
    return complexItem._id;
  }
  itemHeightFn(item, index) { // better performance if setting item height
    return 162;
  }

  getArea() {
    this.area = this.vhQueryCafe.getlocalAreas().filter(item => item.status && item._id != "go_home" && item.id_branch == this.vhQueryCafe.getDefaultBranch()._id)

    if (this.area.length) {
      this.areaID = this.area[0]['_id']
      this.getTable()
    }
  }

  areaChanged(areaID) {
    this.getTable()
  }

  getTable() {
    this.table = this.vhQueryCafe.getlocalTables().filter(item => this.status != null ? (item.id_area == this.areaID && item.status == this.status) : item.id_area == this.areaID)
    this.table = this.transform()
  }

  selectTable(item) {
    this.dismiss(item)
  }

  dismiss(data?) {
    this.vhComponent.hideModal(data)
  }


  transform() {
    if (this.platform.width() >= 1024) return this.transformProd4()
    else if (this.platform.width() >= 599) return this.transformProd3()
    else return this.transformProd2()
  }


  transformProd2() {
    let array: any[] = []
    let arrayRaw = this.table.map((item, index) => {
      return this.table[index + 1] ? [item, this.table[index + 1]] : [item]
    })

    let val = this.table.length
    for (let i = 0, len = val <= 1 ? val : ((val % 2) ? val : (val - 1)); i < len; i += 2) {
      array.push(arrayRaw[i])
    }
    return array;
  }
  transformProd3() {
    let array: any[] = []
    let arrayRaw = this.table.map((item, index) => {
      if (!this.table[index + 1]) return [item]
      else if (!this.table[index + 2]) return [item, this.table[index + 1]]
      else return [item, this.table[index + 1], this.table[index + 2]]
    })
    let val = this.table.length
    for (let i = 0, len = val <= 1 ? val : ((val % 2) ? val : (val - 1)); i < len; i += 3) {
      array.push(arrayRaw[i])
    }
    return array;
  }
  transformProd4() {
    let array: any[] = []
    let arrayRaw = this.table.map((item, index) => {
      if (!this.table[index + 1]) return [item]
      else if (!this.table[index + 2]) return [item, this.table[index + 1]]
      else if (!this.table[index + 3]) return [item, this.table[index + 1], this.table[index + 2]]
      else return [item, this.table[index + 1], this.table[index + 2], this.table[index + 3]]
    })
    let val = this.table.length
    for (let i = 0, len = val <= 1 ? val : ((val % 2) ? val : (val - 1)); i < len; i += 4) {
      array.push(arrayRaw[i])
    }
    return array;
  }
}
