import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { VhQueryCafe, VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from '../../vh-component/vh-component';

@Component({
  selector: 'app-search-category',
  templateUrl: './search-category.component.html',
  styleUrls: ['./search-category.component.scss']
})
export class SearchCategoryComponent {
  @Input() type: number[] = []
  public categoryList: any[] = []
  public searchList: any[] = [];
  constructor(
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef
  ) { }

  ngAfterViewInit() {
    this.categoryList = this.type.length ? this.vhQueryCafe.getlocalCategorys().filter(item => this.type.includes(item.type)) : this.vhQueryCafe.getlocalCategorys()
    this.searchList = [... this.categoryList]
    this.cdRef.detectChanges()
  }

  cancel(value) {
    if (value) this.vhComponent.hideModal()
  }
  selectCategory(value) {
    if (value) this.vhComponent.hideModal(value)
  }

  search(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.categoryList, ['name'])
    else this.searchList = this.categoryList
  }
}
