import { Component } from '@angular/core';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { TranslateService } from '@ngx-translate/core';
import { VhAuth } from 'ionic-vhframeworks';

@Component({
  selector: 'app-search-lang',
  templateUrl: './search-lang.component.html',
  styleUrls: ['./search-lang.component.scss']
})
export class SearchLangComponent {
  public langList: any[] = [
    { name: 'Vietnamese', value: 'vi', img: 'assets/icon/settings/language/vi.svg', country_code : 'VN' },
    { name: 'English', value: 'en', img: 'assets/icon/settings/language/en.svg', country_code : 'US' }
  ];

  public lang: string = this.vhAuth.localStorageGET('language_code');
  constructor(private translateService: TranslateService, private vhComponent: VhComponent,private vhAuth: VhAuth) { }

  public selectLang(value: string) {
    let lang = this.langList.find(item => item.value == value)
    this.vhAuth.localStorageSET('language_code', value)
    this.vhAuth.localStorageSET('country_code', lang.country_code)
    this.translateService.use(value)
  }

  cancel() {
    this.vhComponent.hideModal()
  }

}
